* {
  box-sizing: border-box;
}

body {
  color: #3c3c3c;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.container {
  width: 1024px;
  min-height: 768px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.breed-select {
  height: 40px;
  min-width: 240px;
  color: green;
  text-align: center;
  border: 2px solid rgba(5, 125, 15, .5);
  border-radius: 10px;
  margin-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

select:focus {
  outline-color: green;
}

.cat-info {
  gap: 20px;
  display: flex;
}

img {
  max-width: 500px;
  max-height: 500px;
}

h2 {
  margin-top: 0;
}

.temperament {
  margin-right: 5px;
  font-weight: 700;
}

.loader {
  color: green;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "";
  animation: print 2s linear infinite alternate;
}

.loader:after {
  content: "";
  width: 2px;
  height: 1.3em;
  opacity: .8;
  background: currentColor;
  animation: blink 1s steps(2, end) infinite;
  position: absolute;
  top: 50%;
  right: -4px;
  transform: translateY(-45%);
}

@keyframes blink {
  0% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}

@keyframes print {
  0% {
    content: "L";
  }

  10% {
    content: "Lo";
  }

  20% {
    content: "Loa";
  }

  30% {
    content: "Load";
  }

  40% {
    content: "Loadi";
  }

  50% {
    content: "Loadin";
  }

  60% {
    content: "Loading";
  }

  70% {
    content: "Loading.";
  }

  80% {
    content: "Loading..";
  }

  90%, 100% {
    content: "Loading...";
  }
}

